.Button {
  vertical-align: middle;
  text-align: center;
  border-radius: 20%;
  -webkit-transition: background-color 0.5s;
  -o-transition: background-color 0.5s;
  transition: background-color 0.5s;
}

.Aside { 
  background-color: #df861d;
  color: #dfaa1d;
  grid-area: aside;
}

.Aside-table {
  display: table;
  border-spacing: 20px;
  font-size: x-large;
  height: 100%;
}

.Aside thead {
  font-size: 2em;
  height: 20%;
  color: #894a00;
}

.Header {
  font-size: 3em;
  color: #df861d;
  background-color: #333;
  grid-area: header;
  padding-top: 30px;
}

.Header-title {
  padding-left: 100px;
}

.Content {
  font-size: large;
  color: #ddd;
  grid-area: cont;
  margin-top: 50px;
  margin-bottom: 50px;
  padding-left: 50px;
  padding-right: 50px;
  overflow: auto;
}

.Content img {
  float: right;
  height: 400px;
  border-radius: 100%;
}

h3 {
  padding-top: 20px;
  padding-bottom: 20px;
  color: #df861d;
}

a {
  color: #fdc029;
  text-decoration: none;
}

a:hover {
  color: #894a00;
}

ul {
  list-style: none;
}

li {
  padding-top: 10px;
}

p {
  padding-top: 10px;
}

.Container {
  display: grid;
  grid-template-areas:
    'aside header'
    'aside cont';
  grid-template-rows: 1fr 9fr;
  grid-template-columns: 1fr 4fr;
}



